const isString = str => typeof str === 'string';
const isArray = Array.isArray;
const isObject = obj => typeof obj === 'object' && obj !== null;
const isFunction = obj => typeof obj === 'function';
const assign = Object.assign;

/* eslint-disable no-param-reassign */

/**
 * encode å­ç¬¦ä¸²
 * @param {String} value éè¦è½¬ç çå­ç¬¦ä¸²
 * @return {String} è½¬ç ç»æ
 */
function encode(value) {
  return String(value)
    // .replace(/[^ !'()~*]/g, encodeURIComponent)
    .replace(/[^!'()~*\u{1f300}-\u{1f5ff}\u{1f900}-\u{1f9ff}\u{1f600}-\u{1f64f}\u{1f680}-\u{1f6ff}\u{2600}-\u{26ff}\u{2700}-\u{27bf}\u{1f1e6}-\u{1f1ff}\u{1f191}-\u{1f251}\u{1f004}\u{1f0cf}\u{1f170}-\u{1f171}\u{1f17e}-\u{1f17f}\u{1f18e}\u{3030}\u{2b50}\u{2b55}\u{2934}-\u{2935}\u{2b05}-\u{2b07}\u{2b1b}-\u{2b1c}\u{3297}\u{3299}\u{303d}\u{00a9}\u{00ae}\u{2122}\u{23f3}\u{24c2}\u{23e9}-\u{23ef}\u{25b6}\u{23f8}-\u{23fa}]/ug, encodeURIComponent)
    .replace(/ /g, '+')
    .replace(/[!'()~*]/g, ch => `%${ch.charCodeAt().toString(16).slice(-2).toUpperCase()}`); // eslint-disable-line max-len, newline-per-chained-call
}

/**
 * å°è¯·æ±å¯¹è±¡æ¢æå­ç¬¦ä¸²
 * @param {Object} obj è¯·æ±å¯¹è±¡
 * @return {String} è¯·æ±å­ç¬¦ä¸²
 */
function encodeObj(obj) {
  if (!obj) {
    return '';
  }

  const params = [];

  Object.keys(obj).forEach((key) => {
    let value = obj[key];

    if (value === null || value === undefined) {
      value = '';
    }

    params.push(`${encode(key)}=${encode(value)}`);
  });

  return params.join('&').replace(/%20/g, '+');
}

/**
 * å°è¯·æ±å¯¹è±¡æ¢æ "x-www-form-urlencoded" ç±»åå­ç¬¦ä¸²
 * @param {Object} obj è¯·æ±å¯¹è±¡
 * @return {String} è¯·æ±å­ç¬¦ä¸²
 */
function encodeForm(obj) {
  const flatten = (newObj, key, value) => {
    if (isArray(value) || isObject(value)) {
      Object.keys(value).forEach((k) => {
        flatten(newObj, `${key}[${k}]`, value[k]);
      });
      return newObj;
    }

    newObj[key] = value;
    return newObj;
  };

  return encodeObj(Object.keys(obj).reduce((newObj, key) => flatten(newObj, key, obj[key]), {}));
}

class ObjectUtil {
  constructor(defaults) {
    this.defaults = defaults;
  }

  get(obj) {
    return assign({}, this.defaults, obj);
  }

  set(key, value) {
    if (!key) {
      return;
    }

    if (isString(key)) {
      const keys = key.split('.');
      keys.reduce((obj, k, i) => {
        if (i === keys.length - 1) {
          obj[k] = value;
        }
        return obj[k];
      }, this.defaults);
    }

    if (isObject(key)) {
      Object.keys(key).forEach((k) => {
        this.set(k, key[k]);
      });
    }

    if (isArray(key)) {
      key.forEach(k => this.set(k, value));
    }
  }

  remove(key) {
    if (!key) {
      return;
    }

    if (isString(key)) {
      const keys = key.split('.');
      keys.reduce((obj, k, i) => {
        if (i === keys.length - 1) {
          delete obj[k];
        }
        return obj[k];
      }, this.defaults);
    }

    if (isArray(key)) {
      key.forEach(k => this.remove(k));
    }
  }
}

class FunctionUtil {
  constructor(handler) {
    this.handler = handler;
  }

  set(handler) {
    if (isFunction(handler)) {
      this.handler = handler;
    }
  }

  exec() {
    if (isFunction(this.handler)) {
      this.handler.apply(null, arguments); // eslint-disable-line prefer-rest-params
    }
  }
}

class StringUtil {
  constructor(base) {
    this.base = base || '';
  }

  set(base) {
    if (isString(base)) {
      this.base = base;
    }
  }

  get(str) {
    return this.base + (str || '');
  }
}

class AjaxUtil {

  constructor() {
    // æå¡å¨å°å
    this.ServerUrl = new StringUtil();

    // å¨å± fetch éç½®
    this.Options = new ObjectUtil({
      credentials: 'same-origin',
      timeout: 5000,
    });

    // å¨å±è¯·æ±å¤´
    this.Headers = new ObjectUtil({
      'X-Requested-With': 'XMLHttpRequest',
    });

    // å¨å±è¯·æ±åæ°
    this.Body = new ObjectUtil({});

    // å¨å±ç¶æå¤çæ¹æ³
    this.StatusHandler = new FunctionUtil();

    // å¨å±ç»æå¤çæ¹æ³
    this.ResultHandler = new FunctionUtil(() => true);
    /**
     * å°è¯·æ± url ä¸ params è½¬æ¢æå¸¦ queryString ç url
     * @param {String} url åºæ¬ url
     * @param {Object} params è¯·æ±åæ°
     * @return {String} è¯·æ±å­ç¬¦ä¸²
     */
    this.parseUrl = (url, params) => {
      let queryString = encodeObj(this.Body.get(params));

      if (queryString) {
        if (url.indexOf('?') === -1) {
          queryString = `?${queryString}`;
        } else {
          queryString = `&${queryString}`;
        }
      }

      return url + queryString;
    }

    /**
     * æ£æ¥è¯·æ±çè¿åç¶æç 
     * @param {Object} response æå¡å¨ååºç»æ
     * @return {Object} æ­£å¸¸çååºç»æ
     */
    this.checkStatus = (response) => {
      const status = response.status;

      if ((status >= 200 && status < 300) || status === 304) {
        return response;
      }

      this.StatusHandler.exec(status, response);
      throw new Error(response.statusText);
    }

    /**
     * å°æå¡å¨ç¸åºç»æè½¬æ¢ä¸ºjson
     * @param {Object} response æå¡å¨ååºç»æ
     * @return {Promise} JSONæ°æ®
     */
    this.parseJSON = (response) => {
      return response.json().then(null, (ex) => { // è½¬æ¢JSONå¤±è´¥
        this.StatusHandler.exec(500, response);
        return Promise.reject(ex);
      });
    }

    /**
     * è¯·æ±æååè°å½æ°
     * @param {Object} result æå¡å¨è¿åç»æ
     * @return {Object|Promise} æ­£å¸¸çæ°æ® æè å¤±è´¥çPromise
     */
    this.successCallback = (result) => {

      if (this.ResultHandler.handler(result)) {
        return result;
      }

      return Promise.reject(result);
    }

    /**
     * è¯·æ±å¤±è´¥åè°å½æ°
     * @param {Object} ex å¤±è´¥ä¿¡æ¯
     * @return {Promise} å¤±è´¥çPromise
     */
    this.errorCallback = (ex) => {
      return Promise.reject(ex);
    }

    /**
     * Getè¯·æ±
     * @param {String} url è·¯å¾
     * @param {Object} params åæ°
     * @return {Promise} è¯·æ±Promise
     */
    this.get = (url, params) => {
      const options = this.Options.get({
        headers: this.Headers.get(),
      });

      return fetch(this.ServerUrl.get(this.parseUrl(url, params)), options)
        .then(this.checkStatus)
        .then(this.parseJSON)
        .catch(this.errorCallback);
    }

    /**
     * Getè¯·æ±ï¼è¿åJSONï¼
     * @param {String} url è·¯å¾
     * @param {Object} params åæ°
     * @return {Promise} è¯·æ±Promise
     */
    this.getJSON = (url, params) => {
      const options = this.Options.get({
        headers: this.Headers.get(),
      });

      return fetch(this.ServerUrl.get(this.parseUrl(url, params)), options)
        .then(this.checkStatus)
        .then(this.parseJSON)
        .then(this.successCallback)
        .catch(this.errorCallback);
    }

    /**
     * Postè¯·æ±
     * @param {String} url è·¯å¾
     * @param {Object} params åæ°
     * @return {Promise} è¯·æ±Promise
     */
    this.post = (url, params) => {
      const options = this.Options.get({
        method: 'POST',
        headers: this.Headers.get({
          'Content-Type': 'application/x-www-form-urlencoded',
        }),
        body: encodeForm(this.Body.get(params)),
      });

      return fetch(this.ServerUrl.get(url), options)
        .then(this.checkStatus)
        .then(this.parseJSON)
        .then(this.successCallback)
        .catch(this.errorCallback);
    }

    /**
     * Postè¯·æ±ï¼åéformdataï¼
     * @param {String} url è·¯å¾
     * @param {FormData} formdata åæ°
     * @return {Promise} è¯·æ±Promise
     */
    this.postForm = (url, formdata) => {
      const options = this.Options.get({
        method: 'POST',
        headers: this.Headers.get(),
        body: formdata,
      });

      const data = this.Body.get({});

      Object.keys(data).forEach((d) => {
        formdata.append(d, data[d]);
      });

      return fetch(this.ServerUrl.get(url), options)
        .then(this.checkStatus)
        .then(this.parseJSON)
        .then(this.successCallback)
        .catch(this.errorCallback);
    }

    /**
     * Postè¯·æ±ï¼åéjsonï¼
     * @param {String} url è·¯å¾
     * @param {Object} params åæ°
     * @return {Promise} è¯·æ±Promise
     */
    this.postJSON = (url, params) => {
      const options = this.Options.get({
        method: 'POST',
        headers: this.Headers.get({
          Accept: 'application/json',
          'Content-Type': 'application/json',
        }),
        body: JSON.stringify(this.Body.get(params)),
      });

      return fetch(this.ServerUrl.get(url), options)
        .then(this.checkStatus)
        .then(this.parseJSON)
        .then(this.successCallback)
        .catch(this.errorCallback);
    }

    /**
     * Putè¯·æ±
     * @param {String} url è·¯å¾
     * @param {Object} params åæ°
     * @return {Promise} è¯·æ±Promise
     */
    this.put = (url, params) => {
      const options = this.Options.get({
        method: 'PUT',
        headers: this.Headers.get({
          'Content-Type': 'application/x-www-form-urlencoded',
        }),
        body: encodeForm(this.Body.get(params)),
      });

      return fetch(this.ServerUrl.get(url), options)
        .then(this.checkStatus)
        .then(this.parseJSON)
        .then(this.successCallback)
        .catch(this.errorCallback);
    }

    /**
     * Deleteè¯·æ±
     * @param {String} url è·¯å¾
     * @param {Object} params åæ°
     * @return {Promise} è¯·æ±Promise
     */
    this.remove = (url, params) => {
      const options = this.Options.get({
        method: 'DELETE',
        headers: this.Headers.get(),
      });

      return fetch(this.ServerUrl.get(this.parseUrl(url, params)), options)
        .then(this.checkStatus)
        .then(this.parseJSON)
        .then(this.successCallback)
        .catch(this.errorCallback);
    }

    /**
     * getè¯·æ±
     * @param {String} url è·¯å¾
     * @param {Object} params åæ°
     * @return {Promise} è¯·æ±Promise
     */
    this.getPlain = (url, params) => {
      const options = this.Options.get({
        method: 'GET',
        headers: this.Headers.get(),
      });

      return fetch(this.ServerUrl.get(this.parseUrl(url, params)), options)
        .then(this.checkStatus)
        .then(this.successCallback)
        .catch(this.errorCallback);
    }


    /**
     * postè¯·æ±
     * @param {String} url è·¯å¾
     * @param {Object} params åæ°
     * @return {Promise} è¯·æ±Promise
     */
    this.postPlain = (url, params) => {
      const options = this.Options.get({
        method: 'POST',
        headers: this.Headers.get(),
      });

      return fetch(this.ServerUrl.get(this.parseUrl(url, params)), options)
        .then(this.checkStatus)
        .then(this.successCallback)
        .catch(this.errorCallback);
    }

    /**
     * postäºè¿å¶æ°æ®
     * @param {String} url è·¯å¾
     * @param { ReadableStream } params äºè¿å¶æ°æ®
     * @return {Promise} è¯·æ±Promise
     */
    this.postBinary = (url, params) => {
      const options = this.Options.get({
        method: 'POST',
        headers: this.Headers.get(),
        body: params
      });

      return fetch(this.ServerUrl.get(this.parseUrl(url)), options)
        .then(this.checkStatus)
        .then(this.successCallback)
        .catch(this.errorCallback);
    }

    /**
     * å¯¼åºæä»¶
     * @param {String} url è·¯å¾
     * @param {Object} params åæ°
     */
    this.download = (url, params) => {
      window.open(this.ServerUrl.get(this.parseUrl(url, params)));
    }

  }
}

export default AjaxUtil;